import React, { Component } from 'react';

import NavMobile from './components/ui/navmobile/NavMobile';
import HeroImg from './components/heroimg/HeroImg';
import SideMenu from './components/ui/sidemenu/SideMenu';
import AboutMe from './components/aboutme/AboutMe';
import ProfileImg from './components/profileimg/ProfileImg';
import SkillCards from './components/skillcards/SkillCards';

import ContactForm from './components/contactform/ContactForm';
import Footer from './components/footer/Footer';
import Projects from './components/projects/Projects';
import Modal from './components/ui/modal/Modal';
// import Overlay from './components/ui/overlay/Overlay';
import jump from 'jump.js';
import AbstractGeoImg from './images/backgrounds/abstract-geometric.jpg';

import { easeInOutCubic } from './utils/Easing.js';

class App extends Component {
  state = {
    isFormShowing: false,
  }
 
  modalClosed = () => this.setState({isFormShowing: false});

  viewForm = () => this.setState( (prevState) => ({isFormShowing: !prevState.isFormShowing}));
   
  
  setJump(jumpingTarget) {
    //This will jump based on where the passed setJump function is called from
    jump( jumpingTarget, {
            duration: 800,
            offset: -55,
            easing: easeInOutCubic,
      })
  }

  
  render() {
    let contactForm = null;
    contactForm = <ContactForm />;
    return (
      <div className="container">
        <NavMobile jumpTo={this.setJump} viewForm={this.viewForm} />
        <HeroImg jumpTo={this.setJump} />
        <SideMenu />

        <div style={{position: 'relative'}} className="">
          <div className="overlay"></div>
          <div style={{backgroundImage: `url(${AbstractGeoImg})`,
                       position: 'absolute',
                       top: '0',
                       left: '0',
                       right: '0',
                       bottom: '0',
                       zIndex: '-2' }} />
            <AboutMe />
            <ProfileImg />
            <SkillCards />
        </div>

        <Projects />
        <Modal show={this.state.isFormShowing} modalClosed={this.modalClosed}>
            {contactForm}
          </Modal>
        <Footer viewForm={this.viewForm} jumpTo={this.setJump} />
      </div>
    );
  }
}

export default App;
