import React from 'react';

const styleBackdrop = {
  position: 'fixed',
  top: 0,
  width: '100%',
  height: '100%',
  backgroundColor: 'rgba(0, 0, 0, 0.35)',
  zIndex: '1',

}
 
const backdrop = props => (
<div className="backdrop" style={styleBackdrop} onClick={props.click}> </div>);

export default backdrop;
