import React, { Component } from 'react';

import $ from 'jquery';

class ContactForm extends Component {

  state = {
    name: '',
    email: '',
    subject: '',
    message: '',
    isSubmited: false,
    isFormComplete: null,
  }

  componentDidMount() {
     $(".input-button button").click(function(event) {

     $(".form-wrapper").addClass("send");
   });
  }

  handleNameChange = event => {
    this.setState({
      name: event.target.value,
  });
  }
  handleEmailChange = event => {
    this.setState({
      email: event.target.value,
  });
  }
  handleSubjectChange = event => {
    this.setState({
      subject: event.target.value,
  });
  }
  handleMessageChange = event => {
    this.setState({
      message: event.target.value,
  });
  }
  verifyFormComplete = () => {
    let form = this.state;
    if(form.name && form.email && form.subject && form.message) {
      console.log("Form Was Filled");
      return true;
    }
    else{
      console.log("Form Wan Not Completed!");
      return false;
    }
  }
  handleSubmit = event => {
    event.preventDefault();

    if(this.verifyFormComplete() === true) {
      this.setState({isSubmited: true, 
                     isFailedAttempt: false,
                     isFormComplete: true});
          
    }
      else {
        this.setState({isFailedAttempt: true,
                       isFormComplete: false});
        console.log("Failed Submission");
      }
  }


  render() {
    let thankyoumessage = this.state.isSubmited === true ? <h4>Thank you {this.state.name} for your message!</h4> : null;

    return (
<div className="form-body ">

    <form className="form-wrapper col-md-4 form-header" onSubmit={this.handleSubmit}>
    {thankyoumessage}
    <div style={{display: this.state.isSubmited === false ? 'block' : 'none' }}>
   

          <div>
              <h4>
                  Contact Me Here..
              </h4>
              
          </div>

          <div className="input-text filds">
            <input type="text" id="label1" placeholder="Name" name="name"
              onChange={this.handleNameChange}
              value={this.state.name} />
            <label htmlFor="label1">Name</label>
          </div>

          <div className="input-text filds">
            <input type="text" id="label2" placeholder="E-mail" name="email"
            onChange={this.handleEmailChange}
            value={this.state.email} />
            <label htmlFor="label2">E-mail</label>
          </div>

          <div className="input-text filds">
            <input type="text" id="label3" placeholder="Subject" name="subject"
            onChange={this.handleSubjectChange}
            value={this.state.subject} />
            <label htmlFor="label3">Subject</label>
          </div>

          <div className="input-text filds">
            <input type="text" id="label4" placeholder="Message" name="message"
            onChange={this.handleMessageChange}
            value={this.state.message} />
            <label htmlFor="label4">Message</label>
          </div>

          <div className="contact-me">
            <button type="submit">Submit</button>
          </div>

          <div className="message">
              <span 
               style={{opacity: this.state.isFormComplete === false ? '1' : '0',
                       color: 'white',
                       transition: 'opacity 0.3s',
                       fontFamily: 'Arial'}}>
                  Please Finish The Form 
              </span>
          </div>
    </div>
    </form>
    </div>
    );
  }
} export default ContactForm;
