import React, { Component } from 'react';
import Aux from '../../../hoc/aux/Aux';
import ModalBackdrop from './ModalBackdrop/ModalBackdrop';

class Modal extends Component {
  shouldComponentUpdate ( nextProps, nextState ) {
      return nextProps.show !== this.props.show || nextProps !== this.props.children;
  }

  componentWillUpdate() {
    console.log('[Modal] WillUpdate');
  }

  render() {
    return (
      <Aux>
        <ModalBackdrop show={this.props.show} clicked={this.props.modalClosed} />
          <div className="modal"
            style={{
              width: '100%',
              position: 'fixed',
              top: '85px',
              transform: this.props.show ? 'translateY(0)' : 'translateY(230px)',
              overflow: this.props.show ? 'inherit' : 'hidden',
              opacity: this.props.show ? '1' : '0',
              transition: 'transform 0.33s, opacity 0.09s'
            }}>
            {this.props.children}
          </div>
      </Aux>
    );
  }
}
export default Modal;

 
